.bg-about {
  // height: 100vh;
  position: relative;
  .content {
    color: $black;
    padding: 15px 5%;
    display: flex;
    flex-direction: row;
    .me-profile {
      img {
        width: 150px;
      }
    }
  }
  .batik {
   display: none; 
  }
}

@media screen and (min-width: 500px) {
  .bg-about {
    .batik {
      position: absolute;
      z-index: 1;
      width: 25vw;
      bottom: 10vh;
      right: 10vw;
    }
  } 
}
