.navbar {
  display: flex;
  width: 100vw;
  height: 50px;
  flex-direction: row;
  justify-content: space-between;
  // background-color: salmon;
  position: fixed;
  z-index: 12;
  align-items: center;
  // padding: 0 100px;
  .logo {
    height: 40px;
    margin: 10px 0;
  }
  .navbar-menu {
    .link-btn {
      color: $primary-color;
      &:hover {
        border-bottom: 2.8px solid $primary-color;
        color: $primary-color;
      }
    }
    .active {
      border-bottom: 2.8px solid $primary-color;
      color: $primary-color;
    }
  }
}

.ant-layout-header {
  padding:0 10px;
}

.transparent {
  background-color: transparent;
  transition: 3ms ease-in-out;
}

.dark {
  transition: 3ms ease-in-out;
  background-color: $primary-color;
  height: 50px;
  color: $white;
  .logo {
    height: 40px;
    margin: 10px 0;
  }
  .navbar-menu {
    .link-btn {
      color: $white;
      &:hover {
        border-bottom: 2.8px solid $white;
        color: $white;
      }
    }
    .active {
      border-bottom: 2.8px solid $white;
      color: $white;
    }
  }
}

@media screen and (min-width: 500px ){
  .navbar {
    height: 100px;
    .logo {
      height: 70px;
    }
   
  }
  .ant-layout-header {
    padding:0 50px;
  } 
  .dark {
    height: 50px;
    .logo {
      height: 40px;
      margin: 10px 0;
    }
  }
}
